:root {
  --main-bg-color: rgb(253, 249, 170);
  --main-text-color: rgba(0, 21, 189, 0.87);
  --bar-color: gray;
  --alternate-bg-color: rgb(239, 239, 239);
}

html {
  display: block;
  width: 100%;
}

body {
  align-items: center;
  background-color: var(--main-bg-color);
  box-sizing: border-box;
  color: var(--main-text-color);
  display: flex;
  flex-flow: column;
  font-family: "Source Sans 3", sans-serif;
  font-size: 12px;
  justify-content: center;
  margin: 0 auto;
  padding: 0;
}

#container {
  box-sizing: border-box;
  counter-reset: item;
  display: flex;
  flex-flow: row wrap;
  margin: 36px auto;
  padding: 0;
  width: 100%;
}

#header-container {
  margin: 0 auto;
  padding: 0;
  width: 100%;
}

#header-container #header-row {
  align-items: center;
  background: var(--bar-color);
  box-sizing: border-box;
  display: grid;
  font-family: monospace;
  font-weight: 200;
  grid-template-columns: 1fr 2fr 1fr 1fr 1fr 1fr;
  left: 0;
  list-style: none;
  margin: 0 auto;
  padding: 1px 2px;
  position: fixed;
  width: 100%;
}

#header-row button {
  background: transparent;
  border: transparent;
  font-weight: 400;
  color: #fef9aa;
  cursor: pointer;
  font-family: monospace;
  font-size: 12px;
  padding: 0;
  text-align: left;
}

#header-row button.asc::after { content: " ↑";}

#header-row button.desc::after { content: " ↓";}

#header-row input[type="text"] {
  background: rgba(255, 255, 255, 0.8);
  border: 1px solid rgba(39, 39, 39, 0.66);
  box-sizing: border-box;
  color: #272727;
  font-family: monospace;
  font-size: 12px;
  padding: 2px 4px;
  width: 100%;
}

#header-row input[type="text"]::placeholder {
  color: var(--bar-color);
  opacity: 0.7;
}

#container .row {
  align-items: flex-start;
  box-sizing: border-box;
  display: grid;
  grid-template-columns: 1fr 2fr 1fr 1fr 1fr 1fr;
  justify-content: flex-start;
  list-style: none;
  padding: 3px 2px;
  width: 100%;
  background-color: var(--main-bg-color);
}

#container .row:nth-child(even) {
  background-color: var(--alternate-bg-color);
}

#container li::before {
  align-items: center;
  color: #3c3c3c3b;
  content: counter(item);
  counter-increment: item;
  display: flex;
  font-family: "Arial Narrow", Arial, serif;
  font-size: 1.5rem;
  justify-content: center;
  padding-left: 2px;
  position: absolute;
  width: 100%;
  z-index: -999;
}

#container .row span {
  -webkit-box-orient: vertical;
  display: -webkit-box;
  min-height: 22px;
  -webkit-line-clamp: 2;
  line-clamp: 2;
  overflow: hidden;
  text-overflow: ellipsis;
}

#container .row span[title="who"] a {
  font-weight: 500;
}

a:link:hover {
  text-decoration: none;
}

#info {
  background-color: transparent;
  border: none;
  cursor: pointer;
  font-size: 18px;
  font-weight: 400;
  color: blue;
}

#info-popover {
  background-color: white;
  bottom: 24px;
  margin: 0 auto;
  padding: 12px;
  position: fixed;
  right: 20px;
  visibility: hidden;
  width: 200px;
}

div#pagination {
  width: 100%;
  position: fixed;
  bottom: 0;
  background: var(--bar-color);
  font-family: monospace;
  color: blue;
  display: flex;
  justify-content: space-around;
  align-items: center;
  font-size: 10px;
}

div#pagination button:not(#info) {
  border: none;
  background: transparent;
  font-family: monospace;
  color: #fef9aa;
  vertical-align: middle;
  font-size: 12px;
}

div#pagination button:hover {
  text-decoration: underline;
  cursor: pointer;
}